import React , { useState } from 'react';
import './App.css';
import click_audio from './aud/click.mp3'
import del_audio from './aud/delsound.wav'
const App = () => {
  const [side,setSide] = useState(false);
  const SideBarHandler = () =>{
    let sidebar = document.getElementById('sidebar');
    if(side===false){
      setSide(true)
      sidebar.style.visibility = "visible";
      sidebar.style.width = "191px";
    }else{
      setSide(false)
      sidebar.style.width = "0px"
      sidebar.style.visibility = "hidden";
    }
  }
  const [data,setData] = useState('');
  const InputHandler = e =>{
    setData(e.target.value);
  }
  const audio = new Audio(click_audio);
  const del_aud = new Audio(del_audio);
  const appendData = (e) =>{
    audio.play()
    setData(data.concat(e.target.innerText))
  }
  const DeleteData = () =>{
    audio.play()
    let newData = data.slice(0,-1);
    setData(newData)
  }
  const Evaluate = () =>{
    audio.play()
    let eval_data = eval(data);
    setData(eval_data.toString())
  }
  const Square = () =>{
    audio.play()
    let squareval = (data*data);
    setData(squareval.toString())
  }
  const Cube = () =>{
    audio.play()
    let cubeval = (data*data*data);
    setData(cubeval.toString())
  }
  return (
    <div className='container'>
      <div className='row'>
        <div className='header-container bg-dark'>
          <h1>{!side&&<i className="bi bi-list"onClick={SideBarHandler}></i>}{side&&<i className='bi bi-x'onClick={SideBarHandler}></i>}</h1>
        </div>
      </div>
      <div className='row container-row align-items-center justify-content-center'>
        <div className='col-12 col-lg-3 col-md-9 col-xl-5 main-col 'style={{height:"100vh",padding:"0px"}}>
          <div className='sidebar'id="sidebar">
            <li>About</li>
          </div>
          <div className='content-container'>
            <div><input placeholder='0'className='inputBar'type="text"value={data}onChange={InputHandler}/></div>
            <div className='buttonsContainer'>
              <button className='button'onClick={()=>{del_aud.play();setData('')}}>C</button>
              <button className='button bg-danger text-white'onClick={DeleteData}>DEL</button>
              <button className='button'onClick={Square}>x<sup>2</sup></button> 
              <button className='button bap'onClick={appendData}>+</button>
              <button className='button bap'onClick={appendData}>1</button>
              <button className='button bap'onClick={appendData}>2</button>
              <button className='button bap'onClick={appendData}>3</button>
              <button className='button bap'onClick={appendData}>-</button>
              <button className='button bap'onClick={appendData}>4</button>
              <button className='button bap'onClick={appendData}>5</button>
              <button className='button bap'onClick={appendData}>6</button>
              <button className='button bap'onClick={appendData}>*</button>
              <button className='button bap'onClick={appendData}>7</button> 
              <button className='button bap'onClick={appendData}>8</button>
              <button className='button bap'onClick={appendData}>9</button>
              <button className='button bap'onClick={appendData}>/</button>
              <button className='button'onClick={Cube}>x<sup>3</sup></button>
              <button className='button bap'onClick={appendData}>0</button>
              <button className='button bg-primary text-white'onClick={Evaluate}>=</button>
              <button className='button bap'onClick={appendData}>%</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
